@mixin storyRoot() {
  display: flex;
  margin: 20px 0;
}

@mixin imageWrapper() {
  width: 270px;
}

@mixin slottedImg() {
  width: 260px;
  height: 160px;
}

@mixin regionRight() {
  padding: 0 20px 20px 20px;
}

@mixin h3Slotted() {
  margin-top: 0 !important;
  line-height: 23px;
  font-weight: 500;
}

// ---------- TABLET ---------- //
@mixin storyRootTablet() {
  display: flex;
  padding: 20px 0;
  margin: 0;
  border-top: 2px solid #e5e5e5;
  max-height: 115px;
}

@mixin imageWrapperTablet() {
  max-width: 150px;
}

@mixin slottedImgTablet() {
  width: 150px;
  height: auto;
  margin: 0;
}

@mixin regionRightTablet() {
  margin: 0 65px 0 20px;
  padding: 0;
}

@mixin h3SlottedTablet() {
  overflow: hidden;
  max-height: 115px;
  margin: 0;
}

@mixin descriptionTablet() {
  display: none;
}

// ---------- MOBILE ---------- //
@mixin storyRootMobile() {
  display: flex;
  padding: 20px 0;
  margin: 0;
  border-top: 2px solid #e5e5e5;
  max-height: 60px;
}

@mixin imageWrapperMobile() {
  max-width: 130px;
}

@mixin slottedImgMobile() {
  width: 90px;
  height: auto;
  margin: 0 17px;
}

@mixin regionRightMobile() {
  margin: 0 20px 0 0;
  padding: 0;
}

@mixin h3SlottedMobile() {
  line-height: 18px;
  font-size: 75%;
  overflow: hidden;
  max-height: 55px;
  margin: 0;
}

@mixin descriptionMobile() {
  display: none;
}

// ------- VARIABLE CSS ------- //

@mixin storyTitleStyle() {
  color: var(--story-title-color, #002e5d);
  font-family: var(--story-title-font-family,
      ("Gotham A", "Gotham B", Helvetica, sans-serif));
}

@mixin storyTeaserStyle() {
  font-family: var(--story-teaser-font-family,
      ("Gotham A", "Gotham B", Helvetica, sans-serif));
}
